import {
  Component,
  Input,
  OnInit,
  AfterViewInit,
  ViewChild,
  ElementRef,
} from '@angular/core';
import { fromEvent } from 'rxjs';
import { HelperService } from '../helper.service';

@Component({
  selector: 'app-picture-element',
  templateUrl: './picture-element.component.html',
  styleUrls: ['./picture-element.component.scss'],
})
export class PictureElementComponent implements OnInit, AfterViewInit {
  @Input() imagePath: string = '';
  @Input() imageSet: any;
  @ViewChild('imagecontrol') imagecontrol: ElementRef | undefined;

  public imageSize = '';

  public image = '';

  static instanceCounter = 0;

  constructor(public $helperService: HelperService) {}

  ngOnInit(): void {
    this.imageSize = this.imageSet[0];

    this.image = this.imagePath + this.imageSize;

    if (this.$helperService.isBrowser()) {
      fromEvent(window, 'resize').subscribe((event: any) => {
        console.log('xwidth', window.innerWidth);
        if (window.innerWidth < 800) {
          console.log(
            'xwidth breakpoint'
            // this.imagecontrol?.nativeElement.getAttribute('src')
          );
          this.image = '/assets/projects/camera/camera_ph.gif';
        }
      });
    }
  }

  ngAfterViewInit() {}

  ngOnDestroy() {}

  public imageLoaded(event?: any) {
    const image = event.path[0];
    console.log(
      event.path[0].naturalWidth + ' x ' + event.path[0].naturalHeight,
      event.path[0].width + ' x ' + event.path[0].height
    );
    if (
      this.imageSet.length > 1 &&
      this.imageSet[0].indexOf('_ph') > -1 &&
      image.width < 640
    ) {
      this.imageSize = this.imageSet[2];
      console.log('kleinere bild da', this.imageSet, this.imageSize);
    }
  }
}
